@import url('https://fonts.googleapis.com/css2?family=Imperial+Script&family=Noto+Sans+Mono:wght@400;700&family=Nunito&family=Nunito+Sans:opsz@6..12&family=Pinyon+Script&family=Praise&family=Raleway:wght@400;700&display=swap')
$base-font-size: 16px
*
    margin: 0
    padding: 0
@keyframes loading
    0%
        transform: scale(0.9)
        opacity: 0.4
        // filter: blur(10px)
        // color: #53aceb
    50%
        transform: scale(1)
        opacity: 1
        // filter: none
        // color: #ffee00
    100%
        opacity: 0.4
        transform: scale(0.9)
        // filter: blur(10px)
        // color: #53aceb
html
    height: 100%
body
    height: 100%

    font-family: 'Raleway', sans-serif
    // font-family: 'Nunito Sans', sans-serif
    // font-family: 'Nunito', sans-serif
    // font-family: 'Noto Sans Mono', monospace
    display: flex
    color: #222
    #root
        display: flex
        width: 100%
        .App
            width: 100%
            display: flex
            flex-direction: column
            // background-color: pink
            #game
                position: fixed
                top: 0
                left: 0
                width: 100%
                height: 100%
                display: flex
                justify-content: center
                align-items: center
                background-color: #fff
                z-index: 9
                button
                    line-height: 40px
                    padding: 0 20px
            .loadingModal
                position: fixed
                background-color: #ffffff
                z-index: 9
                backdrop-filter: blur(10px)
                height: 100%
                width: 100%
                display: flex
                justify-content: center
                align-items: center
                transition: 0.5s
                &.loadingClosed
                    pointer-events: none
                    transform: scale(5)
                    opacity: 0
                h3
                    color: #333
                    animation: 5s infinite loading
                img
                    // background-color: pink
                    width: 50px
                    height: 40px
            h1
                height: 40px
                display: flex
                align-items: center
                justify-content: center
                font-family: 'Imperial Script', cursive
                position: fixed
                width: 100%
                // font-family: 'Noto Sans Mono', monospace
                // font-family: 'Nunito', sans-serif
                // font-family: 'Nunito Sans', sans-serif
                // font-family: 'Pinyon Script', cursive
                // font-family: 'Praise', cursive
                // font-family: 'Raleway', sans-serif
                font-weight: normal
                display: none
            #mainText
                // background-color: tomato
                // line-height: 2.5vh
                // height: calc(80% - 40px)
                height: 70%
                width: 100%
                // box-shadow: 0 0 30px black
                // position: absolute
                box-sizing: border-box
                overflow-y: scroll
                padding: 0 10px
                scrollbar-width: 0
                // display: flex
                justify-content: stretch
                flex-wrap: wrap
                align-content: flex-start
                font-size: $base-font-size
                -ms-overflow-style: none
                scrollbar-width: none
                // line-height: 25px
                // padding: 10px
                touch-action: none
                -webkit-touch-callout: none
                -webkit-user-select: none
                -khtml-user-select: none
                -moz-user-select: none
                -ms-user-select: none
                user-select: none
                position: relative
                &::-webkit-scrollbar
                    display: none
                .blank
                    opacity: 0
                    box-sizing: border-box
                    overflow-y: scroll
                    -ms-overflow-style: none
                    scrollbar-width: none
                    line-height: 25px
                    padding: 10px
                    position: absolute
                    top: 0
                    left: 0
                    width: 100%
                    height: 100%
                    background-color: gold
                    pointer-events: none
                    &::-webkit-scrollbar
                        display: none
                pre
                    font-family: 'Nunito', sans-serif
                    // font-family: 'Nunito Sans', sans-serif
                    // font-family: 'Pinyon Script', cursive
                    // font-family: 'Praise', cursive
                    // font-family: 'Raleway', sans-serif
                    touch-action: none
                    display: inline-block
                    // background-color: #fff
                    // box-shadow: 0 0 10px #00000011
                    // border-radius: 100px
                    // touch-action: none
                    // display: inline-block
                    // background-color: tan
                    // padding-right: 9.61px
                    // padding: 0 5px
                    // height: 5%
                    font-size: $base-font-size
                    // font-family: 'Noto Sans Mono', monospace

                    // line-height: 25px
                    // flex: 1
                    // white-space: nowrap
                    // background-color: pink
                    // transition: 0.3s
                    em
                        display: inline-block

            .buttons
                // opacity: 0
                // pointer-events: none
                transition: 0.3s
                text-align: center
                // background-color: gold
                display: flex
                flex-wrap: wrap
                justify-content: center
                // position: fixed
                bottom: 0
                left: 0
                width: 100%
                // background-color: gold
                background-color: #fff
                // border-top: 1px solid #ddd
                // height: 30%
                button
                    height: 40px
                    // height: 5%
                    padding: 0 30px
                    border: 1px solid #999
                    background: white
                    border-radius: 20px
                    background-image: url("./img/arrowIcon.svg")
                    background-size: 100% 20%
                    background-repeat: no-repeat
                    background-position: center
                    transform: rotate(180deg)
                    cursor: pointer
                    -webkit-tap-highlight-color: transparent
                    opacity: 0.5
                    &:active
                        opacity: 1
                    &#arrowDown
                        transform: rotate(0deg)
                    &#hamburgerMenu
                        background-image: url("./img/hamburger-menu.svg")
                        background-size: 100% 50%
                        border: none
                    &#glitter
                        background-image: url("./img/glitter.svg")
                        background-size: 100% 40%
                        border: none
                h6
                    font-weight: normal
                    line-height: 40px
                    padding: 0 10px
                    font-size: $base-font-size
                    color: #aaa
                    font-family: 'Nunito', sans-serif

                    input
                        font-family: 'Nunito', sans-serif

                        color: #aaa
                        font-size: $base-font-size
                        outline: none
                        border: none
                        // background: #f0f0f0
                        padding-left: 7px
                        line-height: 38px
                        // border-radius: 3px
                        box-sizing: border-box
                        &:focus
                            color: #222
            p
                padding: 10px
                width: 100%
                height: calc(30% - 40px)
                // background-color: gold !important
                text-align: left
                box-sizing: border-box
                font-size: $base-font-size
                overflow-y: scroll
                position: relative
                #starButton
                    height: 40px
                    width: 40px
                    border: none
                    border-radius: 50px
                    background-color: #fff
                    position: absolute
                    top: 10px
                    right: 10px
                    background-image: url("./img/star.svg")
                    background-size: 100% 40%
                    background-repeat: no-repeat
                    opacity: 0.4
                    background-position: center
                    cursor: pointer
                    &:active
                        opacity: 0.8
                        // background-color: #00000011